@font-face {
	font-family: "ravicon";
	src: url("../../fonts/ravicon/ravicon.eot?49245023");
	src: url("../../fonts/ravicon/ravicon.eot?49245023#iefix") format("embedded-opentype"), url("../../fonts/ravicon/ravicon.woff2?49245023") format("woff2"), url("../../fonts/ravicon/ravicon.woff?49245023") format("woff"), url("../../fonts/ravicon/ravicon.ttf?49245023") format("truetype"), url("../../fonts/ravicon/ravicon.svg?49245023#ravicon") format("svg");
	font-weight: normal;
	font-style: normal;
}

.ri {
	font-family: "ravicon";
	font-style: normal;
	font-weight: normal;
	display: inline-block;
	text-decoration: inherit;
	text-align: center;

	/* For safety - reset parent styles, that can break glyph codes*/
	font-variant: normal;
	text-transform: none;

	/* fix buttons height, for twitter bootstrap */
	line-height: 1em;

	/* you can be more comfortable with increased icons size */
	/* font-size: 120%; */

	/* Font smoothing. That was taken from TWBS */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	/* Uncomment for 3D effect */
	/* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.ri-arrow-right-long:before { content: '\e800'; } /* '' */
.ri-download:before { content: '\e801'; } /* '' */
.ri-instagram:before { content: '\e802'; } /* '' */
.ri-whatsapp:before { content: '\e803'; } /* '' */
.ri-arrow-up-right:before { content: '\e804'; } /* '' */
.ri-eye:before { content: '\e806'; } /* '' */
.ri-link:before { content: '\e807'; } /* '' */
.ri-play:before { content: '\e809'; } /* '' */